<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!--   -->
      <div slot="header">
        <el-row :gutter="20">
          <el-col :span="6">
            <div class="grid-content bg-purple">
              <el-input placeholder="" v-model="input" clearable></el-input>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="grid-content bg-purple">
              <el-button type="primary"></el-button>
            </div>
          </el-col>
        </el-row>
      </div>
      <!--   -->
      <div>
        <el-table :data="userData" stripe style="width: 100%" border :cell-style="rowClass" :header-cell-style="headClass">
          <el-table-column prop="_id" label="id"></el-table-column>
          <el-table-column prop="role" label="">
            <template slot-scope="slot">
              <el-tag type="primary">{{slot.row.role == '0' ? '' : ''}}</el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="email" label=""></el-table-column>
          <el-table-column prop="state" label="">
            <template slot-scope="slot">
              <el-tag type="primary">{{slot.row.state == '0' ? '' : ''}}</el-tag>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button @click="editUser(scope.row._id)" type="primary" size="mini" icon="el-icon-edit"></el-button>
              <el-button @click="deleteUser(scope.row._id)" type="danger" size="mini" icon="el-icon-delete"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <el-dialog title="" :visible.sync="editUserDialogVisible" width="50%">
          <div>
            <el-form ref="form" :model="userForm" label-width="80px">
              <el-form-item label="id">
                <el-input v-model="userForm._id" :disabled="true"></el-input>
              </el-form-item>
              <el-form-item label="" >
                <el-input v-model="userForm.email" @change="changeemail"></el-input>
              </el-form-item>
              <el-form-item label="" >
                <el-input v-model="userForm.username"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-select v-model="userForm.role">
                  <el-option label="" value="0"></el-option>
                  <el-option label="" value="1"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-select v-model="userForm.state">
                  <el-option label="" value="0"></el-option>
                  <el-option label="" value="1"></el-option>
                </el-select>
              </el-form-item>
            </el-form>
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button @click="editUserDialogVisible = false"> </el-button>
            <el-button type="primary" @click="updateUser"> </el-button>
          </span>
        </el-dialog>
      </div>
      <!--  -->
      <el-pagination :current-page="currentPage" :page-sizes="[5, 10, 15, 20]" :page-size="pageSize"  layout="total, prev, pager, next, jumper" :total="total" @current-change="currentChange"></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      input: '',
      // 
      userData: [],
      // 
      currentPage: 1,
      // 
      pageSize: 4,
      // 
      total: 0,
      // 
      editUserDialogVisible: false,
      // 
      userForm: {}
    }
  },
  created () {
    // 
    this.getUserList()
  },
  methods: {
    // 
    async getUserList () {
      const { data: res } = await this.$http.get('userlist', { params: { currentPage: this.currentPage } })
      // console.log(res)
      this.total = res.count
      this.userData = res.users
      console.log(res.users)
    },
    // 
    async editUser (id) {
      this.editUserDialogVisible = true
      const { data: res } = await this.$http.get('getuser', { params: { id: id } })
      if (res.status !== 200) {
        return this.$message.error('')
      }
      // res.user.role = res.user.role == '0' : '' ? ''
      this.userForm = res.user
      if (this.userForm.role === 0) {
        this.userForm.role = ''
      } else {
        this.userForm.role = ''
      }
      if (this.userForm.state === 0) {
        this.userForm.state = ''
      } else {
        this.userForm.state = ''
      }
      console.log(this.userForm)
    },
    // 
    async updateUser () {
      this.editUserDialogVisible = false
      console.log(this.userForm)
      if (this.userForm.role === '') {
        this.userForm.role = 0
      } else {
        this.userForm.role = 1
      }
      if (this.userForm.state === '') {
        this.userForm.state = 0
      } else {
        this.userForm.state = 1
      }
      const { data: res } = await this.$http.put('updateuser', this.userForm)
      if (res.status !== 200) {
        this.$message.error('')
      }
      this.getUserList()
      // console.log(res)
    },
    // 
    deleteUser: function (id) {
      this.$confirm('', '', {
        distinguishCancelAndClose: true,
        confirmButtonText: '',
        cancelButtonText: ''
      })
        .then(async () => {
          await this.$http.delete('deleteuser', { params: { id: id } })
          this.getUserList()
          // console.log(id)
          this.$message({
            type: 'info',
            message: ''
          })
        })
        .catch(action => {
          this.$message({
            type: 'primary',
            message: action === 'cancel'
              ? ''
              : '2'
          })
        })
    },
    // 
    currentChange: function (currentPage) {
      // console.log(currentPage)
      this.currentPage = currentPage
      this.getUserList()
    },
    // 
    headClass () {
      return 'text-align: center'
    },
    // 
    rowClass () {
      return 'text-align: center'
    },
    changeemail: function (e) {
      console.log(e)
      console.log(this.userForm)
    }
  }
}
</script>

