<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
       <!--  -->
      <div slot="header">
        <el-row :gutter="20">
          <el-col :span="6">
            <div class="grid-content bg-purple">
              <el-input placeholder="" v-model="input" clearable></el-input>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="grid-content bg-purple">
              <el-button type="primary"></el-button>
            </div>
          </el-col>
        </el-row>
      </div>
      <!-- <div>
        <div>
          <el-radio-group v-model="radio">
            <el-radio-button disabled label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
          </el-radio-group>
        </div>
         <div style="margin-top: 15px;">
          <el-radio-group v-model="radio">
            <el-radio-button disabled label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
          </el-radio-group>
        </div>
         <div style="margin-top: 15px;">
          <el-radio-group v-model="radio">
            <el-radio-button disabled label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
          </el-radio-group>
        </div>
      </div> -->
    </el-card>
    <el-card class="box-card" style="margin-top: 15px;">
      <!--  -->
      <div>
        <el-table :data="articleData" stripe fit style="width: 100%" border :cell-style="rowClass" :header-cell-style="headClass">
          <!-- <el-table-column prop="_id" label="id"></el-table-column> -->
          <el-table-column prop="publishDate" :formatter="dateFormat" label=""></el-table-column>
          <el-table-column prop="title" label=""></el-table-column>
          <el-table-column prop="classify" label=""></el-table-column>
          <el-table-column prop="isPublish" label="">
            <template slot-scope="slot">
              <el-switch @change="changePublish(slot.row)" v-model="slot.row.isPublish"></el-switch>
            </template>
          </el-table-column>
          <el-table-column prop="isPublish" label="">
            <template slot-scope="slot">
              <el-switch @change="changeHot(slot.row)" v-model="slot.row.isHot"></el-switch>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button @click="editArticle(scope.row._id)" type="primary" size="mini" icon="el-icon-edit"></el-button>
              <el-button @click="deleteArticle(scope.row._id)" type="danger" size="mini" icon="el-icon-delete"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <!--  -->
      </div>
      <!--  -->
      <el-pagination :current-page="currentPage" :page-sizes="[1, 2, 3, 4]" :page-size="pageSize" layout="total, prev, pager, next, jumper" :total="total" @current-change="currentChange"></el-pagination>
    </el-card>
  </div>
</template>

<script>
import moment from 'moment'
export default {
  data () {
    return {
      // 
      input: '',
      // 
      articleData: [],
      // 
      currentPage: 1,
      // 
      pageSize: 4,
      // 
      total: 0,
      radio: '',
      // 
      publishArticleDialogVisible: false,
      // 
      deleteUserDialogVisible: false,
      publishValue: true,
      isPublish: ''
    }
  },
  created () {
    this.getArticleList()
  },
  methods: {
    // 
    async getArticleList () {
      const { data: res } = await this.$http.get('articlelist', { params: { currentPage: this.currentPage } })
      // console.log(res)
      this.total = res.count
      this.articleData = res.articles
      console.log(res.articles)
    },
    // 
    editArticle: function (id) {
      console.log(id)
      this.$router.push('/editarticle?id=' + id)
    },
    // 
    deleteArticle: function (id) {
      this.$confirm('', '', {
        distinguishCancelAndClose: true,
        confirmButtonText: '',
        cancelButtonText: ''
      })
        .then(async () => {
          await this.$http.delete('deletearticle', { params: { id: id } })
          this.getArticleList()
          // console.log(id)
          this.$message({
            type: 'info',
            message: ''
          })
        })
        .catch(action => {
          this.$message({
            type: 'primary',
            message: action === 'cancel'
              ? ''
              : '2'
          })
        })
    },
    // 
    currentChange: function (currentPage) {
      // console.log(currentPage)
      this.currentPage = currentPage
      this.getArticleList()
    },
    // 
    async changePublish (row) {
      const { data: res } = await this.$http.put('changepublish', row)
      // console.log(res)
      if (res.status !== 200) {
        this.$message.error('')
      } else {
        this.$message.success('')
      }
      this.getArticleList()
    },
    // 
    async changeHot (row) {
      const { data: res } = await this.$http.put('changehot', row)
      // console.log(res)
      if (res.status !== 200) {
        this.$message.error('')
      } else {
        this.$message.success('')
      }
      this.getArticleList()
    },
    // 
    headClass () {
      return 'text-align: center'
    },
    // 
    rowClass () {
      return 'text-align: center'
    },
    // 
    dateFormat: function (row, column) {
      const date = row[column.property]
      if (date === undefined) { return '' }
      return moment(date).format('YYYY-MM-DD')
    }
  }
}
</script>

