<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!--  -->
      <div slot="header">
        <el-row :gutter="20">
          <el-col :span="6">
            <div class="grid-content bg-purple">
              <el-input placeholder="" v-model="title" clearable></el-input>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="grid-content bg-purple">
              <el-input placeholder="" v-model="url" clearable></el-input>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="grid-content bg-purple">
              <el-button type="primary" @click="addFriendly"></el-button>
            </div>
          </el-col>
        </el-row>
      </div>
      <div>
        <!--  -->
        <el-table :data="friendlyData" stripe style="width: 100%" border :cell-style="rowClass" :header-cell-style="headClass">
          <el-table-column prop="_id" label="id"></el-table-column>
          <el-table-column prop="time" :formatter="dateFormat" label=""></el-table-column>
          <el-table-column prop="title" label=""></el-table-column>
          <el-table-column prop="url" label=""></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button @click="editFriendly(scope.row._id)" type="primary" size="mini" icon="el-icon-edit"></el-button>
              <el-button @click="deleteFriendly(scope.row._id)" type="danger" size="mini" icon="el-icon-delete"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <el-dialog title="" :visible.sync="editFriendlyDialogVisible" width="50%">
          <div>
            <el-form ref="form" :model="friendlyForm" label-width="80px">
              <el-form-item label="id">
                <el-input v-model="friendlyForm._id" disabled></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="friendlyForm.title"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="friendlyForm.url"></el-input>
              </el-form-item>
            </el-form>
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button @click="editFriendlyDialogVisible = false"> </el-button>
            <el-button type="primary" @click="updateFriendly"> </el-button>
          </span>
        </el-dialog>
      </div>
    </el-card>
  </div>
</template>

<script>
import moment from 'moment'
export default {
  data () {
    return {
      // 
      input: '',
      // 
      friendlyData: [],
      // 
      editFriendlyDialogVisible: false,
      // 
      friendlyForm: [],
      title: '',
      url: ''
    }
  },
  created () {
    this.getFriendlyList()
  },
  methods: {
    // 
    async getFriendlyList () {
      const { data: res } = await this.$http.get('friendlylist')
      console.log(res)
      this.friendlyData = res.friendly
    },
    // 
    async editFriendly (id) {
      const { data: res } = await this.$http.get('getfriendly', { params: { id: id } })
      if (res.status !== 200) {
        this.$message.error('')
      }
      this.$message.success('')
      // this.getFriendlyList()
      console.log(res)
      this.friendlyForm = res.friendly
      this.editFriendlyDialogVisible = true
    },
    // 
    updateFriendly: async function () {
      this.editFriendlyDialogVisible = false
      const { data: res } = await this.$http.put('updatefriendly', this.friendlyForm)
      if (res.status !== 200) {
        this.$message.error('')
      }
      this.$message.success('')
      this.getFriendlyList()
    },
    // 
    deleteFriendly: function (id) {
      this.$confirm('', '', {
        distinguishCancelAndClose: true,
        confirmButtonText: '',
        cancelButtonText: ''
      })
        .then(async () => {
          await this.$http.delete('deletefriendly', { params: { id: id } })
          this.getFriendlyList()
          // console.log(id)
          this.$message({
            type: 'info',
            message: ''
          })
        })
        .catch(action => {
          this.$message({
            type: 'primary',
            message: action === 'cancel'
              ? ''
              : '2'
          })
        })
    },
    // 
    addFriendly: async function () {
      const { data: res } = await this.$http.post('friendly', { title: this.title, url: this.url })
      this.title = ''
      this.url = ''
      this.getFriendlyList()
      if (res.status !== 200) {
        this.$message.error('')
      } else {
        this.$message.success('')
      }
    },
    // 
    headClass () {
      return 'text-align: center'
    },
    // 
    rowClass () {
      return 'text-align: center'
    },
    // 
    dateFormat: function (row, column) {
      const date = row[column.property]
      if (date === undefined) { return '' }
      return moment(date).format('YYYY-MM-DD HH:MM:ss')
    }
  }
}
</script>

