<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!--  -->
      <div slot="header">
        <el-row :gutter="20">
          <el-col :span="6">
            <div class="grid-content bg-purple">
              <el-input placeholder="" v-model="classifyValue" clearable></el-input>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="grid-content bg-purple">
              <el-button type="primary" @click="addClassify"></el-button>
            </div>
          </el-col>
        </el-row>
      </div>
      <div>
        <!--  -->
        <el-table :data="classifyData" stripe style="width: 100%" border :cell-style="rowClass" :header-cell-style="headClass">
          <el-table-column prop="_id" label="id"></el-table-column>
          <el-table-column prop="time" :formatter="dateFormat" label=""></el-table-column>
          <el-table-column prop="classify" label=""></el-table-column>
          <!-- <el-table-column prop="state" label=""></el-table-column> -->
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button @click="getClasify(scope.row._id)" type="primary" size="mini" icon="el-icon-edit"></el-button>
              <el-button @click="deleteClassify(scope.row._id)" type="danger" size="mini" icon="el-icon-delete"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <el-dialog title="" :visible.sync="editUserDialogVisible" width="50%">
          <div>
            <el-form ref="form" :model="classifyForm" label-width="80px">
              <el-form-item label="id">
                <el-input v-model="classifyForm._id" disabled></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="classifyForm.classify"></el-input>
              </el-form-item>
            </el-form>
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button @click="editUserDialogVisible = false"> </el-button>
            <el-button type="primary" @click="editClasify"> </el-button>
          </span>
        </el-dialog>
      </div>
    </el-card>
  </div>
</template>

<script>
import moment from 'moment'
export default {
  data () {
    return {
      // 
      classifyValue: '',
      // 
      classifyData: [],
      // 
      classifyForm: {},
      // 
      editUserDialogVisible: false
    }
  },
  created () {
    this.getClassifyList()
  },
  methods: {
    // 
    async getClassifyList () {
      const { data: res } = await this.$http.get('classifylist')
      console.log(res)
      this.classifyData = res.classifylist
    },
    // 
    getClasify: async function (id) {
      this.editUserDialogVisible = true
      const { data: res } = await this.$http.get('getclassify', { params: { id: id } })
      console.log(res)
      if (res.status !== 200) {
        this.$message.error('')
      } else {
        this.classifyForm = res.classify
      }
    },
    // 
    editClasify: async function (id) {
      this.editUserDialogVisible = false
      const { data: res } = await this.$http.put('editclassify', this.classifyForm)
      this.getClassifyList()
      if (res.status !== 200) {
        this.$message.error('')
      } else {
        this.$message.success('')
      }
      // const { data: res } = await this.$http.get('getclassify', { params: { id: id } })
      // console.log(res)
      // if (res.status !== 200) {
      //   this.$message.error('')
      // } else {
      //   this.classifyForm = res.classify
      // }
    },
    // 
    deleteClassify: function (id) {
      this.$confirm('', '', {
        distinguishCancelAndClose: true,
        confirmButtonText: '',
        cancelButtonText: ''
      })
        .then(async () => {
          await this.$http.delete('deleteclassify', { params: { id: id } })
          this.getClassifyList()
          // console.log(id)
          this.$message({
            type: 'info',
            message: ''
          })
        })
        .catch(action => {
          this.$message({
            type: 'primary',
            message: action === 'cancel'
              ? ''
              : '2'
          })
        })
    },
    // 
    addClassify: async function () {
      const { data: res } = await this.$http.post('classify', { classify: this.classifyValue })
      this.classifyValue = ''
      this.getClassifyList()
      if (res.status !== 200) {
        this.$message.error('')
      } else {
        this.$message.success('')
      }
    },
    // 
    headClass () {
      return 'text-align: center'
    },
    // 
    rowClass () {
      return 'text-align: center'
    },
    // 
    dateFormat: function (row, column) {
      const date = row[column.property]
      if (date === undefined) { return '' }
      return moment(date).format('YYYY-MM-DD HH:mm:ss')
    }
  }
}
</script>

