<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span></span>
      </div>
      <div>
        <!--  -->
        <el-input placeholder="" v-model="title">
          <template slot="prepend"></template>
        </el-input>
        <!--  -->
        <el-input style="margin-top: 15px;" placeholder="" v-model="description">
          <template slot="prepend"></template>
        </el-input>
        <!--  -->
        <el-input style="margin-top: 15px;" placeholder="" v-model="cover">
          <template slot="prepend"></template>
        </el-input>
        <el-row :gutter="10">
          <!--   -->
          <el-col :span="14">
            <!--  -->
            <div class="tagForm">
              <span class="tag-title"></span>
              <el-tag :key="tag" v-for="tag in dynamicTags" closable :disable-transitions="false" @close="handleClose(tag)">{{tag}}</el-tag>
              <el-input class="input-new-tag" v-if="inputVisible" v-model="inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm" @blur="handleInputConfirm"></el-input>
              <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
            </div>
          </el-col>
          <!--  -->
          <el-col :span="4">
            <div class="grid-content bg-purple">
              <el-select v-model="value" @change="getClassify" placeholder="" style="margin-top: 15px;">
                <el-option v-for="item in options" :key="item._id" :label="item.classify" :value="item.classify"></el-option>
              </el-select>
            </div>
          </el-col>
          <!--  -->
          <el-col :span="4">
            <div class="grid-content bg-purple">
              <el-select v-model="isPublishValue" placeholder="" style="margin-top: 15px;">
                <el-option v-for="item in isPublish" :key="item.value" :label="item.label" :value="item.value"
                ></el-option>
              </el-select>
            </div>
          </el-col>
          <!--  -->
          <el-col :span="2">
             <el-button type="primary" class="submitBtn" @click="addArticle"></el-button>
          </el-col>
        </el-row>
      </div>
      <!--  -->
      <div class="rich-text-test">
        <vue-html5-editor :content="content" @change="updateData" :height="500" :z-index="1000" :auto-height="true" :show-module-name="false"></vue-html5-editor>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      title: '',
      cover: '',
      description: '',
      options: [],
      isPublish: [
        {
          value: 1,
          label: ''
        },
        {
          value: 0,
          label: ''
        }
      ],
      isPublishValue: '',
      value: '',
      classify: '',
      dynamicTags: [],
      inputVisible: false,
      inputValue: '',
      content: ''
    }
  },
  created () {
    this.getClassifyList()
  },
  methods: {
    // 
    async getClassifyList () {
      const { data: res } = await this.$http.get('classifylist')
      this.options = res.classifylist
      // console.log(res)
    },
    addArticle: async function () {
      const addForm = {
        title: this.title,
        cover: this.cover,
        description: this.description,
        content: this.content,
        tag: this.dynamicTags,
        classify: this.classify,
        isPublish: Boolean(this.isPublishValue)
      }
      console.log(addForm)
      const { data: res } = await this.$http.post('article', addForm)
      console.log(addForm)
      if (res.status !== 200) {
        // console.log('')
        this.$message.error('')
        return
      }
      console.log('')
      this.$message.success('')
      // window.sessionStorage.setItem('token', res.token)
      // this.$router.push('/home')
    },
    // 
    getClassify (e) {
      this.classify = e
    },
    handleClose (tag) {
      this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1)
    },
    showInput () {
      this.inputVisible = true
      this.$nextTick((_) => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    handleInputConfirm () {
      const inputValue = this.inputValue
      if (inputValue) {
        this.dynamicTags.push(inputValue)
      }
      this.inputVisible = false
      this.inputValue = ''
    },
    updateData (e) {
      this.content = e
      // console.log(e)
    }
  }
}
</script>

<style lang="less" scroped>
  .tagForm {
    margin-top: 15px;
    height: 40px;
    line-height: 40px;
    .tag-title {
      margin-right: 10px;
    }
  }
  .el-tag + .el-tag {
    margin-left: 10px;
  }
  .button-new-tag {
    margin-left: 10px !important;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .input-new-tag {
    width: 90px !important;
    margin-left: 10px;
    vertical-align: bottom;
  }
  .rich-text-test {
    height: 400px;
  }
  .vue-html5-editor {
    height: 100%;
    margin-top: 15px;
  }
  .submitBtn {
    margin-top: 15px !important;
  }
</style>
